html,
body {
  margin: 0;
  padding: 0;
  font-family: "Rubik-Regular", "Arial", "Tahoma", sans-serif;
  font-weight: 400;
  font-style: normal;
  text-rendering: optimizelegibility;
  overflow-x: hidden;
  background-color: #f5f5f5;
}

header,
section,
footer,
main {
  width: 100%;
}

.wrapper {
  width: 100%;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 100%;
  max-width: 140rem;
  padding: 0 1.5rem;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.no-scroll {
  overflow: hidden;
}
@media (min-width: 1024px) {
  .no-scroll {
    padding-right: 1.7rem;
  }
}

.overlay {
  position: fixed;
  z-index: 9000;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: none;
  background: var(--light-gray);
  opacity: 0.8;
  will-change: display;
  transition: display 0.5s ease-out;
}
.overlay--active {
  display: block;
}

.fixed {
  position: fixed !important;
  z-index: 200 !important;
  top: 0;
  left: 0;
  background: #fff;
  box-shadow: 0 1.2rem 0.8rem 0rem rgba(34, 60, 80, 0.2);
  will-change: auto;
  transition: all 0.5s ease-out;
}
@media (min-width: 1320px) {
  .fixed {
    width: calc(100% - 17px) !important;
  }
}

.fixed-wrap {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  will-change: auto;
  transition: all 0.5s ease-out;
}

.btn {
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: #fff;
  line-height: 1.5;
  white-space: nowrap;
  border-radius: 10rem;
  outline: none;
  border: none;
  background-color: #FE5F00;
  cursor: pointer;
}

.header {
  padding: 2rem 0 0 0;
  background-color: #fff;
}
.header__inner {
  width: 100%;
  border-bottom: 0.1rem solid #000;
}
.header__inner-top, .header__inner-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.footer {
  height: 40rem;
  background-color: #000;
}

.logo {
  display: flex;
  justify-content: center;
  flex: 1;
  text-transform: uppercase;
  text-align: center;
}
.logo a {
  font-size: 2.4rem;
  font-weight: 700;
  color: #FE5F00;
}

.phone {
  flex: 1;
  font-size: 2rem;
  color: #000;
}

.btn-cart {
  height: 6rem;
  display: inline-flex;
  flex-shrink: 0;
  min-width: 16.2rem;
  padding: 0.6rem 2.1rem 0.6rem 0.5rem;
}
.btn-cart__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.btn-cart__content span:nth-of-type(1) {
  font-size: 1.1rem;
}

.btn-user {
  margin: 0 auto 0 2rem;
}

.btn-heart {
  margin-left: auto;
}

.header__text {
  flex: 2;
  text-align: center;
}

.header__btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 2;
  will-change: box-shadow;
  transition: box-shadow 0.5s ease-in-out;
}
.header__btns .btn-user,
.header__btns .btn-heart {
  width: 4rem;
  height: 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__btns .btn-user:hover, .header__btns .btn-user:focus,
.header__btns .btn-heart:hover,
.header__btns .btn-heart:focus {
  box-shadow: 0px 4px 8px 0px rgba(34, 60, 80, 0.2);
}
.header__btns svg {
  fill: #fff;
}

.main-nav {
  width: 70%;
}
.main-nav ul {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.main-nav ul li a {
  font-size: 1.8rem;
  font-weight: 600;
  padding: 0.5rem;
  color: #000;
}

.header__input-wrap {
  position: relative;
  width: 25%;
}

.header__search {
  width: 100%;
  height: 4rem;
  padding: 0.5rem 4rem 0.5rem 1rem;
  border: 0.1rem solid #000;
  border-radius: 2rem;
}
.header__search-btn {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  width: 3rem;
  height: 3rem;
  padding: 0;
  background: transparent;
  border: none;
}

.product-nav {
  width: 100%;
  overflow: auto;
  margin-top: 3rem;
  border-radius: 1.2rem;
  background-color: #fff;
}
.product-nav::-webkit-scrollbar {
  height: 0.4rem;
  width: 0.4rem;
  border: 0.1rem solid #d5d5d5;
}
.product-nav::-webkit-scrollbar-thumb:horizontal {
  background: #FE5F00;
  border-radius: 1rem;
}
.product-nav ul {
  width: 100%;
  display: flex;
  padding: 2rem 1rem;
}
.product-nav ul li:not(:first-of-type) {
  margin-left: 3rem;
}
.product-nav ul li a {
  padding: 1.2rem;
  font-size: 1.8rem;
  font-weight: 400;
  color: #000;
  border-radius: 1rem;
  will-change: background-color;
  transition: background-color 0.5s ease-in-out;
}
.product-nav ul li a:hover, .product-nav ul li a:focus {
  background-color: #C0C0C0;
}
.product-nav .active {
  background-color: #C0C0C0;
}

.product-card {
  max-width: 32.7rem;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  padding-bottom: 1rem;
  background-color: #fff;
  will-change: box-shadow;
  transition: box-shadow 0.3 ease-in-out;
}
.product-card:hover, .product-card:focus {
  box-shadow: 0.4rem 0.4rem 0.8rem 0rem rgba(34, 60, 80, 0.2);
}
.product-card__img {
  position: relative;
  max-width: 33rem;
  border-radius: 1rem;
  overflow: hidden;
}
.product-card__img::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.2);
  will-change: display;
  transition: opacity 0.3s ease-in-out;
}
.product-card__img:hover::after, .product-card__img:focus::after {
  opacity: 1;
}
.product-card__inner {
  padding: 0 0.5rem 1rem;
}
.product-card__inner__name {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.product-card__title {
  position: relative;
  width: 90%;
  margin: 1rem 0 0.5rem 0;
  font-size: 1.8rem;
  font-weight: 600;
  color: #000;
}
.product-card__info {
  position: absolute;
  right: -3rem;
  top: 0;
  width: 2rem;
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 0.1rem solid #000;
}
.product-card__weight-wrap {
  display: flex;
  align-items: center;
}
.product-card__weight-wrap svg {
  margin-top: 0.2rem;
  fill: #FE5F00;
  stroke: #FE5F00;
  stroke-width: 1rem;
}
.product-card__weight {
  margin-left: 0.5rem;
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 400;
  color: #000;
}
.product-card__price {
  font-size: 1.8rem;
  font-weight: 600;
  color: #000;
}
.product-card__top, .product-card__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.product-card__top {
  margin-bottom: 1rem;
}
.product-card__btns {
  border: solid 0.1rem #FE5F00;
  display: flex;
  align-items: center;
  border-radius: 1.2rem;
  line-height: 4rem;
}
.product-card__minus, .product-card__plus {
  width: 4rem;
  height: 4rem;
  font-size: 2.2rem;
  line-height: 4rem;
  font-weight: 600;
  color: #000;
  border: none;
  border-radius: 1.2rem;
  background-color: transparent;
  will-change: background-color;
  transition: background-color 0.3s ease-in-out;
}
.product-card__minus:hover, .product-card__minus:focus, .product-card__plus:hover, .product-card__plus:focus {
  background-color: #C0C0C0;
}
.product-card__count {
  font-size: 2.2rem;
  line-height: 4rem;
}
.product-card__cartbtn {
  width: 12rem;
  padding: 1rem 0;
  font-size: 1.8rem;
  font-weight: 600;
  will-change: box-shadow;
  transition: box-shadow 0.3s ease-in-out;
}
.product-card__cartbtn:hover, .product-card__cartbtn:focus {
  box-shadow: 0rem 0.8rem 1rem 0rem rgba(34, 60, 80, 0.2);
}
.product-card__total {
  position: relative;
  margin: 1.2rem 0 0 0;
  font-size: 1.8rem;
  font-weight: 600;
  color: #000;
}
.product-card__total::before {
  position: absolute;
  content: "Сумма";
  top: -1.4rem;
  right: 0;
  width: 100%;
  text-align: right;
  font-size: 1.2rem;
  font-weight: 300;
  color: #000;
}

.product-cards {
  width: 100%;
  display: grid;
  margin-top: 2rem;
  grid-template-columns: repeat(auto-fill, minmax(290px, 327px));
  gap: 3rem 2rem;
  justify-content: center;
}

.products {
  padding-bottom: 4rem;
}

.product {
  padding: 4rem 0 6rem 0;
}
.product__inner {
  width: 100%;
  display: grid;
  grid-template-rows: 1fr;
}
@media (min-width: 1024px) {
  .product__inner {
    grid-template-columns: 1fr 1fr;
    column-gap: 3rem;
    align-items: start;
    padding-bottom: 60rem;
  }
}
.product img {
  height: 100%;
  border-top-right-radius: 2rem;
  border-top-left-radius: 2rem;
}
.product__bottom {
  padding: 0 2rem 4rem 2rem;
}
@media (min-width: 1024px) {
  .product__bottom {
    padding: 0 4rem 4rem 4rem;
  }
}
.product__info, .product__action {
  border-radius: 2rem;
  background-color: #fff;
}
.product__desc, .product__spec {
  margin: 4rem 0;
  font-size: 3rem;
  font-weight: 700;
  color: #000;
}
.product__desc-list {
  margin-left: 2rem;
  list-style-image: url("../img/icons/check.svg");
}
.product__desc__item {
  position: relative;
  font-size: 1.8rem;
  font-weight: 400;
  color: #000;
}
.product__desc__item:not(:last-of-type) {
  margin-bottom: 1rem;
}
.product__spec-list {
  width: 100%;
}
.product__spec-item {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.product__spec-item:not(:last-of-type) {
  margin-bottom: 2rem;
}
.product__spec-item div {
  border-bottom: 0.1rem dotted #747678;
  flex-grow: 1;
  transform: translateY(-0.4rem);
}
.product__action {
  position: sticky;
  top: 5rem;
  padding: 2rem;
  margin-top: 2rem;
}
@media (min-width: 1024px) {
  .product__action {
    margin-top: 0;
    padding: 4rem;
  }
}
.product__title {
  margin-bottom: 1rem;
  font-size: 3.4rem;
  font-weight: 700;
  color: #000;
}
.product__weight-wrap {
  display: flex;
  align-items: center;
  margin-bottom: 4rem;
}
.product__weight-wrap svg {
  margin-top: 0.2rem;
  fill: #FE5F00;
  stroke: #FE5F00;
  stroke-width: 1rem;
}
.product__weight {
  margin-left: 0.5rem;
  display: inline-block;
  font-size: 1.8rem;
  font-weight: 400;
  color: #000;
}
.product__price {
  margin-bottom: 4rem;
  font-size: 2.2rem;
  font-weight: 700;
  color: #000;
}
.product__price span {
  margin-left: 1rem;
}
.product__btns-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}
.product__btns {
  min-width: 12rem;
  display: inline-flex;
  justify-content: space-between;
  border: solid 0.1rem #FE5F00;
  border-radius: 1.2rem;
  line-height: 4rem;
}
.product__minus, .product__plus {
  width: 4rem;
  height: 4rem;
  font-size: 2.4rem;
  line-height: 4rem;
  font-weight: 600;
  color: #000;
  border: none;
  border-radius: 1.2rem;
  background-color: transparent;
  will-change: background-color;
  transition: background-color 0.3s ease-in-out;
}
.product__minus:hover, .product__minus:focus, .product__plus:hover, .product__plus:focus {
  background-color: #C0C0C0;
}
.product__total {
  position: relative;
  margin: 1.2rem 0 0 0;
  font-size: 2rem;
  font-weight: 600;
  color: #000;
}
.product__total::before {
  position: absolute;
  content: "Сумма";
  top: -1.8rem;
  right: 0;
  width: 100%;
  text-align: right;
  font-size: 1.4rem;
  font-weight: 300;
  color: #000;
}
.product__cartbtn {
  width: 90%;
  max-width: 37.2rem;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  padding: 1rem 0;
  font-size: 2rem;
  font-weight: 600;
  will-change: box-shadow;
  transition: box-shadow 0.3s ease-in-out;
}
.product__cartbtn:hover, .product__cartbtn:focus {
  box-shadow: 0rem 0.8rem 1rem 0rem rgba(34, 60, 80, 0.2);
}